.backdrop {
  position: fixed;
  padding-top: 6rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.modal {
  border: none;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgb(197, 15, 15);
  padding: 0;
  overflow: hidden;
}
